function color() {
  if (
    loginId.value.length > 0 &&
    loginId.value.indexOf("@") !== -1 &&
    loginPw.value.length >= 5
  ) {
    loginBtn.style.backgroundColor = "#FD8234";
    loginBtn.disabled = false;
  } else {
    loginBtn.style.backgroundColor = "#9a9a9a";
    loginBtn.disabled = true;
  }
}

